.product-cover-133-sec-icon {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  height: 300px;
  flex-shrink: 0;
  object-fit: cover;
}
.product-actions-sec- {
  overflow: hidden;
  margin-top: -64px;
}
.fixed-height {
  align-self: stretch;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.link-product-category {
  position: relative;
  letter-spacing: 0.2px;
  line-height: 24px;
}
.frame-1-sec-,
.frame-1-sec-wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.frame-1-sec-wrapper {
  width: 190px;
  justify-content: space-between;
}
.h5-product-title-1-sec- {
  position: relative;
  font-size: var(--h5-size);
  letter-spacing: 0.1px;
  line-height: 24px;
  display: inline-block;
  color: var(--text-color);
  width: 131px;
  height: 48px;
  flex-shrink: 0;
}
.we-focus-on {
  margin: 0;
}
.paragraph-product-description-container {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.2px;
  line-height: 20px;
  color: var(--second-text-color);
}
.icn-arrow-right-icn-xs {
  position: relative;
  width: 9.14px;
  height: 16px;
  flex-shrink: 0;
  overflow: hidden;
}
.button-sm-sec-,
.frame-parent {
  overflow: hidden;
  gap: var(--gap-3xs);
}
.button-sm-sec- {
  border-radius: var(--br-18xl);
  border: 1px solid var(--primary-text-color);
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-xl);
  align-items: center;
  justify-content: flex-start;
}
.frame-parent {
  align-self: stretch;
  padding: var(--padding-6xl) var(--padding-6xl) var(--padding-16xl);
  align-items: flex-start;
}
.cardpostos1,
.frame-parent,
.product-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.product-card {
  align-self: stretch;
  background-color: var(--light-background-color);
  overflow: hidden;
  align-items: flex-start;
}
.cardpostos1 {
  flex: 1;
  align-items: center;
  text-align: left;
  font-size: var(--h6-size);
  color: var(--primary-text-color);
  font-family: var(--h6);
}
