.h2-section-title-2-sec-,
.h6-section-tag-sec- {
  position: relative;
  letter-spacing: 0.2px;
  line-height: 24px;
}
.h2-section-title-2-sec- {
  font-size: var(--h2-size);
  line-height: 50px;
  color: var(--text-color);
}
.paragraph-sec- {
  position: relative;
  letter-spacing: 0.2px;
  line-height: 20px;
  color: var(--second-text-color);
}
.main-content1,
.rowmedicamentos {
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
}
.main-content1 {
  width: 691px;
  display: flex;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.rowmedicamentos {
  align-self: stretch;
}
.cardmedicamento1,
.rowcards,
.rowmedicamentos {
  display: flex;
  justify-content: flex-start;
}
.cardmedicamento1 {
  width: 240px;
  flex-shrink: 0;
  flex-direction: column;
  align-items: center;
}
.rowcards {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--gap-11xl);
  font-size: var(--h5-size);
  color: var(--text-color);
}
.medicamentos1,
.medicamentos2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.medicamentos2 {
  width: 1050px;
  padding: var(--padding-93xl) 0;
  box-sizing: border-box;
  gap: var(--gap-93xl);
}
.medicamentos1 {
  align-self: stretch;
  background-color: var(--light-background-color);
  height: 796px;
  flex-shrink: 0;
  overflow: hidden;
  text-align: left;
  font-size: var(--h6-size);
  color: var(--primary-text-color);
  font-family: var(--h6);
}
