@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --h6: Montserrat;

  /* font sizes */
  --h6-size: 14px;
  --h5-size: 16px;
  --h2-size: 40px;
  --font-size-xl: 20px;
  --font-size-29xl: 48px;
  --h3-size: 24px;

  /* Colors */
  --light-background-color: #fff;
  --color-gray: #fafafa;
  --second-text-color: #737373;
  --text-color: #252b42;
  --primary-text-color: #8d5cf6;
  --faded-primary-color: #f6f7fb;
  --danger-color: #e74040;
  --color-gainsboro: #e6e6e6;
  --color-whitesmoke: #f9f9f9;

  /* Gaps */
  --gap-xl: 20px;
  --gap-3xs: 10px;
  --gap-77xl: 96px;
  --gap-11xl: 30px;
  --gap-mini: 15px;
  --gap-93xl: 112px;
  --gap-21xl: 40px;
  --gap-22xl: 41px;
  --gap-29xl: 48px;

  /* Paddings */
  --padding-6xl: 25px;
  --padding-141xl: 160px;
  --padding-11xl: 30px;
  --padding-93xl: 112px;
  --padding-16xl: 35px;
  --padding-3xs: 10px;
  --padding-xl: 20px;
  --padding-21xl: 40px;
  --padding-4xs: 9px;
  --padding-3xl-5: 22.5px;
  --padding-117xl: 136px;
  --padding-7xs-1: 5.1px;

  /* border radiuses */
  --br-181xl: 200px;
  --br-18xl: 37px;
  --br-8xs: 5px;

  /* Effects */
  --accentued-drop-shadow: 0px 13px 19px rgba(0, 0, 0, 0.07);
}
