.icon-cool-icon-1654 {
  position: relative;
  width: 72px;
  height: 72px;
  flex-shrink: 0;
  overflow: hidden;
}
.h5-feature-title-1-sec- {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.1px;
  line-height: 24px;
}
.fixed-width-fixed-height-sec- {
  position: relative;
  background-color: var(--danger-color);
  width: 50px;
  height: 2px;
  flex-shrink: 0;
}
.paragraph-feature-description {
  align-self: stretch;
  position: relative;
  font-size: var(--h6-size);
  letter-spacing: 0.2px;
  line-height: 20px;
  color: var(--second-text-color);
}
.card {
  align-self: stretch;
  background-color: var(--light-background-color);
  box-shadow: var(--accentued-drop-shadow);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-16xl) var(--padding-21xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--h5-size);
  color: var(--text-color);
  font-family: var(--h6);
}
