.h5-sec-,
.link-sec- {
  position: relative;
  letter-spacing: 0.1px;
  line-height: 24px;
}
.link-sec- {
  letter-spacing: 0.2px;
}
.col-md-2,
.div1 {
  flex-direction: column;
  align-items: flex-start;
}
.div1 {
  display: flex;
  justify-content: flex-start;
  gap: var(--gap-3xs);
  font-size: var(--h6-size);
  color: var(--second-text-color);
}
.col-md-2 {
  width: 148px;
  flex-shrink: 0;
  gap: var(--gap-xl);
}
.col-md-2,
.col-md-7-sec-,
.div,
.footer1,
.row {
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
}
.row {
  flex-direction: row;
  align-items: flex-start;
}
.col-md-7-sec-,
.div,
.footer1 {
  flex-direction: column;
}
.footer1 {
  width: 1050px;
  padding: 50px 0;
  box-sizing: border-box;
  align-items: flex-start;
}
.col-md-7-sec-,
.div {
  flex-shrink: 0;
}
.div {
  align-self: stretch;
  background-color: var(--light-background-color);
  height: 272px;
  align-items: center;
}
.col-md-7-sec- {
  width: 600px;
  align-items: flex-start;
}
.ant-designfacebook-filled,
.ant-designtwitter-outlined {
  position: relative;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  overflow: hidden;
}
.ant-designtwitter-outlined {
  height: 19.76px;
}
.social-media {
  flex-direction: row;
  justify-content: center;
  gap: var(--gap-xl);
}
.col-md-6,
.row1,
.social-media {
  overflow: hidden;
  display: flex;
  align-items: center;
}
.col-md-6 {
  width: 236px;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: flex-start;
}
.row1 {
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
}
.container {
  width: 1050px;
  padding: var(--padding-6xl) 0;
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
}
.container,
.div2,
.footer,
.medical-affordable {
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.div2 {
  align-self: stretch;
  background-color: var(--color-gray);
  height: 74px;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  font-size: var(--h6-size);
  color: var(--second-text-color);
}
.footer,
.medical-affordable {
  justify-content: flex-start;
}
.footer {
  align-self: stretch;
  background-color: var(--color-gray);
  align-items: center;
}
.medical-affordable {
  position: relative;
  background-color: var(--light-background-color);
  width: 100%;
  align-items: flex-start;
  text-align: left;
  font-size: var(--h5-size);
  color: var(--text-color);
  font-family: var(--h6);
}
