.testimonial-user-cover-1-icon {
  position: relative;
  width: 128px;
  height: 128px;
  flex-shrink: 0;
  overflow: hidden;
  object-fit: cover;
}
.circle-sec- {
  border-radius: var(--br-181xl);
  width: 128px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.paragraph-sec-2 {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.2px;
  line-height: 20px;
}
.h5-user-title {
  position: relative;
  letter-spacing: 0.1px;
  line-height: 24px;
}
.frame-1-sec-1 {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 0 var(--padding-6xl);
  align-items: center;
  justify-content: flex-start;
  font-size: var(--h5-size);
  color: var(--text-color);
}
.card-content {
  align-self: stretch;
  overflow: hidden;
  padding: var(--padding-11xl);
  gap: var(--gap-mini);
}
.card-content,
.medicocard,
.testimonials-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.testimonials-card {
  align-self: stretch;
  background-color: var(--light-background-color);
  overflow: hidden;
  padding: var(--padding-6xl) var(--padding-6xl) 0;
}
.medicocard {
  flex: 1;
  text-align: center;
  font-size: var(--h6-size);
  color: var(--second-text-color);
  font-family: var(--h6);
}
