.h2-section-title-3-sec-,
.h6-section-tag-sec-1 {
  position: relative;
  letter-spacing: 0.2px;
  line-height: 24px;
}
.h2-section-title-3-sec- {
  font-size: var(--h2-size);
  line-height: 50px;
  color: var(--text-color);
}
.paragraph-sec-1 {
  position: relative;
  letter-spacing: 0.2px;
  line-height: 20px;
  color: var(--second-text-color);
}
.main-content2,
.rowpostos {
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
}
.main-content2 {
  width: 691px;
  display: flex;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.rowpostos {
  align-self: stretch;
}
.postos1,
.postos2,
.rowcardspostos,
.rowpostos {
  display: flex;
  justify-content: flex-start;
}
.rowcardspostos {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--gap-11xl);
}
.postos1,
.postos2 {
  flex-direction: column;
  align-items: center;
}
.postos2 {
  width: 1050px;
  padding: var(--padding-93xl) 0;
  box-sizing: border-box;
  gap: var(--gap-93xl);
}
.postos1 {
  align-self: stretch;
  background-color: var(--faded-primary-color);
  height: 1128px;
  flex-shrink: 0;
  overflow: hidden;
  text-align: left;
  font-size: var(--h6-size);
  color: var(--primary-text-color);
  font-family: var(--h6);
}
