.h2-section-title-4-sec-,
.h6-section-tag-sec-2 {
  position: relative;
  letter-spacing: 0.2px;
  line-height: 24px;
}
.h2-section-title-4-sec- {
  font-size: var(--h2-size);
  line-height: 50px;
  color: var(--text-color);
}
.paragraph-sec-3 {
  position: relative;
  letter-spacing: 0.2px;
  line-height: 20px;
  color: var(--second-text-color);
}
.main-content3 {
  width: 691px;
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-3xs);
}
.main-content3,
.mdicos,
.medicos1,
.row3,
.rowmedicos {
  display: flex;
  justify-content: flex-start;
}
.rowmedicos {
  align-self: stretch;
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
}
.mdicos,
.medicos1,
.row3 {
  align-items: center;
}
.row3 {
  align-self: stretch;
  flex-direction: row;
  gap: var(--gap-11xl);
  text-align: center;
  color: var(--second-text-color);
}
.mdicos,
.medicos1 {
  overflow: hidden;
  flex-direction: column;
}
.medicos1 {
  width: 1050px;
  padding: var(--padding-141xl) 0;
  box-sizing: border-box;
  gap: var(--gap-77xl);
}
.mdicos {
  align-self: stretch;
  background-color: var(--light-background-color);
  height: 865px;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--h6-size);
  color: var(--primary-text-color);
  font-family: var(--h6);
}
